       <div class="mobile_only">
          <h2 v-if="$global.global_ru"><span>ЧТО ИЗМЕНИЛОСЬ:</span></h2>
          <h2 v-if="$global.global_kz"><span>Не өзгерді:</span></h2>
          <p v-if="$global.global_ru">Принципиальных изменений в системе государственного информационного заказа в последние годы не происходило. К нововведениям можно отнести тот факт, что Министерство информации исключило госинфорзаказ из системы госзакупок, и СМИ стали получать деньги через решение специальной комиссии. Также Министерство информации разработало и опубликовало критерии СМИ для участия в конкурсе на получение госинформзаказа. </p> 
          <p v-if="$global.global_kz">Соңғы жылдары мемлекеттік ақпараттық тапсырыс жүйесінде принципиальды өзгеріс бола қоймады. Жаңашылдықтарға тек ақпарат министрлігінің мемлекеттік ақпараттық тапсырысты мемлекеттік сатып алу жүйесінен алып тастағанын жатқызуға болады. БАҚ ақшаны арнайы комисияның шешімі бойынша ала алады.</p> 
