      <!-- RU version main text -->
      <div v-if="$global.global_ru">  
          <p>В остальном процедуры остались теми же: государственные СМИ получают деньги по линии «государственного задания» (более 40 млрд тенге ежегодно), частные республиканские СМИ – по линии Министерства информации (более 2 млрд). Региональные СМИ участвуют в местных тендерах акиматов (около 10 млрд тенге).</p>  
        
          <h2><span>Основные проблемы:</span></h2>
      
          <p>Практически все опрошенные отметили, что получают госинформзаказ в том или ином объеме. У кого-то процент дохода от госинформзаказа составляет 60%, у кого-то – 20%. Как правило, в каждом регионе есть одна русскоязычная и одна казахоязычная газета, считающиеся официальными изданиями местных акиматов.</p> 

          <p>Также в последнее время распространена практика создания медиа-холдингов, которые объединяют несколько газет, телеканалов и сетевых изданий. Однако отзывы журналистов о работе таких холдингов – в основном негативные. Они отметили, что в руководство приходят совершенно далекие от журналистики люди, без опыта, квалификации и профессионализма, но с большим желанием контролировать. Как правило, у директора такой конторы есть несколько заместителей. «Они просто мешают нам работать. Каждый считает, что должен внести в нашу работу что-то свое, в итоге они лезут туда, где ничего не смыслят и тормозят работу. Вместо эффективного бизнес-управления мы получили ненужную громоздкую надстройку по отмыванию денег» - говорят журналисты. Они также обратили внимание на то, что большая часть денег из бюджета уходит на зарплаты раздутого штата директора и его заместителей. </p> 
          
          <p class="qoute">«С прошлого года убрали все гонорары. Средняя зарплата – 115 000 тенге. В районных СМИ – 65 000. Раньше платили 6% от стоимости заказных статей, сейчас ничего нет. Все схемы поломаны». </p> 

          <p>Немало разногласий насчет тематических направлений. Одни журналисты говорили, что госзаказ нужен для позитивного освещения политики региона, и их издания это понимают и стараются делать это максимально естественно. Другие отмечали, что у заказчиков зачастую бывает слишком много «рвения»: «акимат требует освещения «Рухани жангыру», но не понимает, что именно им нужно. Они хотят сплошной позитив, а мы пытаемся объяснить, что так нельзя. Исключительно позитив и восхваление – это ненормально». В то же время журналисты признают, что материалы по госзаказу нередко пишутся «для галочки»: «Журналист пишет ужасно, не работает над текстом и заголовками. Его задача – отчитаться, что материал вышел. Что дальше – никого не интересует».</p>  

          <p>По мнению многих опрошенных, тематические направления должны быть максимально сужены: «Государство имеет право продвигать свои идеи, но делать это без давления и цензуры. И нет смысла платить за новости, потому что СМИ делают их в любом случае. Государство должно платить за такие материалы, как например фильм про ВИЧ Юрия Дудя. Социально важные, востребованные, актуальные. Чтобы был результат. А нашему государству больше интересен процесс…»</p> 

          <p>Кроме того, периодически заказчики не хотят принимать работы, которые им не нравятся или «не в рамках». Как правило, заказчики сами определяют границы того, что можно говорить, а что нет. Большинство журналистов отметило, что имеет место цензура, когда акимат вмешивается в редакционную политику издания, контролирует, указывает, что и как освещать. Сильно развито право «телефонного звонка». </p> 

          <p>Насчет необходимости системы госзаказа мнения также разделились. Некоторые считают, что это развращающая политика, оправдание глупости и непрофессионализма. Но в то же время журналисты говорят, что если отменить госзаказ, то 80% СМИ закроются, поскольку не готовы конкурировать («не тот уровень»).</p> 

          <p>Что касается конкуренции в этой сфере, респонденты отметили, что в последнее время появилось много фирм-однодневок, которые изображают солидные издания и нередко забирают многомиллионные лоты. В одном издании сообщили, что после обсуждения и жалоб сотрудники акимата стали более четко и грамотно писать техспецификацию.</p>  

          <p>Среди опрошенных были СМИ, которые принципиально не участвуют в конкурсах госзаказа, потому что считают, что это неминуемо приведет к цензуре. Некоторые публикуют платные материалы, которые заказывают им региональные государственные органы.</p> 

          <p>Журналисты казахоязычных СМИ отметили, что часто приходится заниматься подготовкой «нечитаемого и несмотрибельного контента». Есть немало запретных тем, цензуры и ограничений. Журнаисты считают, что в рамках госзаказа нужно поддерживать действительно нужные и важные для общества темы.</p>   

          <p class="qoute">«Сидят две головы и час болтают – это и выполнение госзаказа. А где качество, где творчество, и в конце концов, где сама журналистика?»</p> 

          <p>Еще одна проблема - госзаказ выигрывают компании из других регионов, которые совсем не знают, что творится в том регионе где объявляется конкурс по госзаказу. «Это просто способ заработка, который не имеет никакого отношения к госинформзаказу».</p> 
        
                  
          <div class="highlight">
          <h2><span>Что делать?</span></h2>
          <ul>
              <li>Ограничить список тем и лотов, делая акцент на аналитику, но не на новости.</li>
              <li>Полностью исключить все формы цензуры (вмешательство в редакционную политику, право телефонного звонка, прямые указания, как освещать те или иные события). </li>
              <li>Усовершенствовать процедуры распределения госзаказа (сроки, лоты, техспецификация).</li>
          </ul>
          </div>
      </div>


      <!-- KZ version main text -->
      <div v-if="$global.global_kz">  
          <p>Ақпарат министрлігі сонымен бірге, мемлекеттік тапсырысты алу конкурсына қатысатын БАҚ-қа критерийлерді әзірлеп жариялады. Басқасы өзгеріссіз қалды: мемлекеттік БАҚ «мемлекеттік тапсырма» (жылына 40 млрд теңге), республикалық жеке БАҚ – ақпарат министрлігі арқылы (жылына 2 млрд-тан астам қаржы алып келеді. Аймақтық БАҚ жергілікті әкімдік тендерлеріне қатысады (шамамен 10 млрд теңге). </p>  
        
          <h2><span>Негізгі мәселелер: </span></h2>
      
          <p>Сауалнамаға қатысқандардың барлық дерлігі мемлекеттік тапсырыс алатынын жасырмады. Кейбірінде мемлекеттік ақпараттық тапсырыстың жалпы кірістегі үлесі 60%, кейбірінде – 20%. Әдеттегісінше, әр өңірде жергілікті әкімдіктің ресми басылымы саналатын қазақ және орыс тілдерінде жарық көретін газеттері болады.</p> 

          <p>Сонымен бірге, соңғы жылдары бірнеше газет пен телеарна, желілік басылымдарды біріктіретін медиа-холдингтер ашу тәжрибесі кеңінен тарады. Алайда, осындай холдингтердің жұмысына қатысты журналистердің пікірі, негізінен, негативті. Олардың айтуынша, басшылыққа журналистикадан хабары, тәжірибесі, біліктілігі және кәсібилілігі жоқ, алайда бақылауды жақсы көретін жандар келеді. Мұндай кеңсе директорының әдеттегідей бірнеше орынбасары болады. «Олар бізге кәдімгідей жұмыс істетпейді. Әрқайсысы біздің жұмысқа бірдеңе әкелгісі келеді, ақырында ештеңе түсінбесе де, барлығына араласады, нәтижесінде жұмысымызға кедергі келтіреді. Тиімді бизнес басқарудың орнына ақша жымқыратын құрылымға тап болдық па деген ойлар мазалайды» - дейді тілшілер. Сонымен бірге, журналистер байқағандай, бюджеттен бөлінген қаражаттың басым бөлігі журналистердің жалақысына емес, басшылар мен олардың орынбасарларының жалақысына жұмсалады. </p> 
          
          <p class="qoute">«Былтырдан бері барлық қаламақыны алып тастады. Орташа жалақы – 115 000 теңге. Аудандық басылымдарда – 65 000. Бұрын тапсырыс материалдар құнының  6% төленсе, қазір ол жоқ. Жүйе өзгеріске ұшырады». </p> 

          <p>Тақырып бағыттарында келіспеушіліктер аз емес. Журналистердің пайымынша, мемлекеттік тапсырыс өңір саясатын оң жағынан көрсету үшін керек, олар қызмет ететін басылымдар мұны жақсы түсінеді әрі шынайы етіп жасағысы келеді. Ал өзгелері тапсырыс берушілердің шамадан тыс кететінін айтады: «әкімдік «Рухани жаңғыруды» жариялау керек дейді, бірақ қалай жариялау керектігін түсінбейді. Бірыңғай позитив пен мақтау – дұрыс емес». Өз кезегінде журналистер мемлекеттік тапсырыс аясында материалдардың есеп үшін жазылатынын жасырмайды: «Журналист қалай болса, солай жаза салады, мәтінмен, тақырыппен жұмыс істемейді. Оның мақсаты – материал шықты деп есеп беру. Әрі қарай не болғаны ешкімді қызықтырмайды». </p>  

          <p>Сауалнамаға тартылғандардың дені тақырып бағыттарының неғұрлым аз болғанын жөн көреді: «Мемлекет өз идеяларын еш қысымсыз және цензурасыз ұсына алады. Ақпарат үшін ақы төлеудің қажеті жоқ,  өйткені БАҚ онсыз да соны жасайды. Мемлекет Юрий Дудьтің АИТВ туралы филмі секілді материалдарға төлеу керек. Әлеуметтік маңызы бар, сұранысқа ие, өзекті. Нәтижесі болу керек. Ал біздің мемлекетке процесс қана қызық...»</p> 

          <p>Мұнан өзге, тапсырыс берушілер өздеріне ұнамаған немесе «қалыпқа сыймайтын» жұмысты қабылдамай қояды. Әдеттегісінше, нені айтуға болады, нені болмайды деп шек қойып тастайды. Журналистердің дені басылым редакциясының саясатына араласқан кезде цензура жасалатынын, бақылау күшейетінін, нені қалай жазу керектігін үйрететінін айтады. Жоғарыдан «қоңырау шалу» жиілейді. </p> 

          <p>Мемлекеттік тапсырыс жүйесінің қажеттілігіне қатысты пікірлер екіге бөлінеді. Кейбірі журналистикаға нұқсан келтіретін саясат, ақымақтық мен кәсіби сауатсызды ақтап алу деп санайды. Бірқатары егер мемлекеттік тапсырысты алып тастаса, 80% БАҚ бәселестікке шыдамай жабылып тынады деген пікірде («деңгейі солай»).</p> 

          <p>Осы саладағы бәсекелестікке қатысты айтар болсақ, респонденттердің айтуынша, соңғы жылдары өздерін салмақты ақпарат құралы ретінде көрсететін, алайда біркүндік фирмалар қаптап кеткен. Олар көп жағдайда миллиондаған лоттарды әкетеді. Бір басылым қызметкері талқылау мен шағымдардан кейін әкімдік қызметкерлері техспецификацияны бұрынғыдан да нақты әрі сауатты жаза бастаған. </p>  

          <p>Сауалнамаға жұмылдырылғандардың арасында принципиалды түрде мемлекеттік тапсырыс конкурстарына қатыспайтын БАҚ бар болып шықты, өйткені, олардың пайымынша, мемтапсырыс цензураға әкеліп соғады. Кейбір редакция аймақтық мемлекеттік органдар тапсырыс берген ақылы материалдарды жариялайды. </p> 

          <p>Қазақтілді БАҚ журналистері көп жағдайда «оқырмайтын және ешкім көрмейтін контент» әзірлейтінін айтады. Тыйым салынған, цензура мен шектеуге ұшырайтын тақырыптар аз емес. Журналистер мемлекеттік тапсырыс аясында расымен де қоғам үшін қажетті және маңызды тақырыптарды қолдау керек.</p>   

          <p class="qoute">«Екі адам бір сағат әңгіме айтады – мемлекеттік тапсырыс деген осы. Материалдың сапасы қайда, шығармашылық қайда, әрі-беріден соң журналистика қайда деген сұрақ туындайды».</p> 

          <p>Тағы бір мәселе – мемлекеттік тапсырысты көп жағдайда конкурс жарияланған өңірге қатысы жоқ компаниялар ұтып алып жатады. «Бұл жай мемлекеттік ақпараттың тапсырысқа ешқандай қатысы жоқ ақша жасау жолы ғана».</p> 
        
                  
          <div class="highlight">
